export const preSubject =
  'To frame guidelines and to direct pharmacies in Tamil Nadu to regulate sale of Emergency Contraceptive Pills';
export const generateEmail = (name, presentLoc, presentPos, extra) => {
  const text = `Dear Sir/Madam,%0D%0A
  I write to you in furtherance of letter dated 02.10.2020 from Riya Gupta, Chennai, requesting you to frame guidelines and to direct pharmacies in Tamil Nadu to regulate sale of Emergency Contraceptive Pills as there is a shadow ban and is therefore unavailable.
  I'm ${name} and I'm a resident of ${presentLoc}.%0D%0A ${
    presentPos ? `I work as a/an ${presentPos}.%0D%0A` : ''
  }${extra ? `${extra}%0D%0A` : ''}`;
  return text;
};
export const generateEmailWithoutBreaks = (
  name,
  presentLoc,
  presentPos,
  extra
) => {
  const text = `Dear Sir/Madam, 
  I write to you in furtherance of letter dated 02.10.2020 from Riya Gupta, Chennai, requesting you to frame guidelines and to direct pharmacies in Tamil Nadu to regulate sale of Emergency Contraceptive Pills as there is a shadow ban and is therefore unavailable. ${
    name !== '' ? `My name is ${name}` : ''
  } ${presentLoc !== '' ? `and I am a resident of ${presentLoc}.` : ''} ${
    presentPos ? `I work as a/an ${presentPos}.` : ''
  } ${
    extra
      ? `

  ${extra}`
      : ''
  }
 `;
  return text;
};
