import React from 'react';

export const Body = () => {
  return (
    <section className='section '>
      <div className='container'>
        <h1 className='title is-size-3 is-size-4-mobile has-text-white has-background-primary'>
          Why is it a nightmare to find an emergency contraceptive pill in Tamil
          Nadu?
        </h1>
        <p className='content is-size-5 is-size-6-mobile has-text-black  is-family-secondary'>
          Since 2016 Tamil Nadu has had a shadow ban on these pills which causes
          pharmacists to simply say they’re out of stock or that they don’t sell
          these pills.
        </p>
        <h1 className='title is-size-3 is-size-4-mobile has-text-white has-background-primary'>
          Why has this been happening?
        </h1>
        <p className='content is-size-5 is-size-6-mobile has-text-black  is-family-secondary'>
          In 2016, a law came out to regulate the sale of abortion pills over
          the counter. Due to the misconception that emergency contraceptive
          pills and abortion pills are the same thing, shipments of ECPs were
          seized.
        </p>
        <h1 className='title is-size-3 is-size-4-mobile has-text-white has-background-primary'>
          What are emergency contraceptive pills?
        </h1>
        <p className='content is-size-5 is-size-6-mobile has-text-black  is-family-secondary'>
          More commonly known as iPill or Unwanted 72, They are pills that women
          take within 72 hours of unprotected sex to prevent an unwanted
          pregnancy.
        </p>
        <h1 className='title is-size-3 is-size-4-mobile has-text-white has-background-primary'>
          How do they work?
        </h1>
        <p className='content is-size-5 is-size-6-mobile has-text-black  is-family-secondary'>
          If taken within 72 hours it prevents or delays ovulation ie, It
          prevents the release of the egg from the ovary if taken early in the
          menstrual cycle, or prevents the embryo from implanting by creating an
          unsuitable environment in the womb.
        </p>
        <h1 className='title is-size-3 is-size-4-mobile has-text-white has-background-primary'>
          Why are they different from abortion pills?
        </h1>
        <p className='content is-size-5 is-size-6-mobile has-text-black  is-family-secondary'>
          They cannot disturb a pregnancy that’s already established.
        </p>
        <h1 className='title is-size-3 is-size-4-mobile has-text-white has-background-primary'>
          Why should you care about this shadow ban?
        </h1>
        <p className='content is-size-5 is-size-6-mobile has-text-black  is-family-secondary'>
          From 2016 there has been a rise in unwanted pregnancies, which has
          lead to higher abortion rates in our state. These ECP’s are crucial
          and essential to help women prevent these unwanted pregnancies without
          having to go through with an abortion / MTP.
        </p>
        <h1 className='title is-size-3 is-size-4-mobile has-text-white has-background-primary'>
          What do pharmacies have to say about this unofficial ban?
        </h1>
        <p className='content is-size-5 is-size-6-mobile has-text-black  is-family-secondary'>
          Most of the pharmacies think that it’s still “illegal” to sell ECP’s
          and are hence scared to do so. Even though officials have said there
          is no official ban on these pills, there is still a confusion with the
          pharmacies.
        </p>
      </div>
    </section>
  );
};
